.main-box{
    background: rgba(4, 2, 26, 0.5);
    height: 550px;
    width: 600px;
    border-radius: 10px;
    box-shadow: 0 15px 25px rgba(0,0,0,.6);
}
.register{
    font-size: 25px;
    font-family:Poppins;
    display: flex;
    justify-content: center;
    color: aliceblue;
}
.inputs{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5vh;
    width: 100%;
    top: 2vh;
    position: relative;
}
input{
    width: 15vw;
    height: 4vh;
    background-color:#383838;
    border: 0px;
    outline: none;
    border-radius: 5px;
    color: white;
}
::placeholder{
    color: white;
    font-size: 17px;
    font-family:Poppins;
}
.submit{
    display: flex;
    justify-content: center;
    
}
button{
    top: 7vh;
    position: relative;
    height: 6vh;
    font-size: 1.4vw;
    width: 10vw;
    font-family:Poppins;
    background-color: #0c0e42;
    border: 0px;
    cursor: pointer;
    color: white;
    font-weight: 300;
    border-radius: 6px;
}
button:hover{
    background-color: black;
    transition: 0.5s;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type=number] {
    -moz-appearance: textfield;
  }
.inputs p{
    font-size: 20px;
    margin: 0;
    color: rgb(223, 214, 214);
    padding: 0;
}

.button1 button{
    height: 7vh;
    width: 12vw;
    margin-bottom: 7vh;
}