.card {
    height: 20vh;
    width: 20vw;
    display: flex;
    align-items: center;
    padding: 10px;
    background: rgba(4, 2, 26, 0.5);
    border-radius: 10px;
    box-shadow: 0 15px 25px rgba(0,0,0,.6);
  }
  
  .left-side {
    flex-grow: 1;
    font-size: 18px;
  }
  
  .right-side {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    position: relative;
    gap: 2vh;
    bottom: 6vh;
  }
  button {
    cursor: pointer;
    border-radius: 5px;
    margin-top: 5px;
    
    height: 5vh;
    width: 5vw;
    font-size: 20px;
    border: 0px;
  }
  .right-side button:nth-child(1):hover{
    background-color: green;
    transition: 0.2s;
  }
  .right-side button:nth-child(2):hover{
    background-color: red;
    transition: 0.2s;
  }